<template>
    <v-navigation-drawer v-model="$store.state.ui.quickbar.open" app temporary right
        :width="$store.state['ui'].quickbar.options.width"
        :hide-overlay="!$store.state['ui'].quickbar.options.overlay"
        :stateless="!$store.state['ui'].quickbar.options.autoclose"
        @input="onChange">

        <slot name="default"></slot>

        <template v-if="!this.$slots.default">
            <div class="x-quickbar fill-height">
                <v-system-bar height="35" dark>
                    <v-icon v-if="$store.state['ui'].quickbar.options.icon" size="20" class="mr-2" v-text="$store.state['ui'].quickbar.options.icon"></v-icon>

                    <span v-if="$store.state['ui'].quickbar.options.title" v-text="$store.state['ui'].quickbar.options.title"></span>

                    <v-btn v-if="!$store.state['ui'].quickbar.options.title && !$store.state['ui'].quickbar.options.icon" @click="$quickbarToggle(false)" icon small>
                        <v-icon size="22" class="mr-0">mdi-close</v-icon>
                    </v-btn>

                    <v-spacer></v-spacer>

                    <v-btn v-if="$store.state['ui'].quickbar.options.title || $store.state['ui'].quickbar.options.icon" @click="$quickbarToggle(false)" icon small>
                        <v-icon size="22" class="mr-0">mdi-close</v-icon>
                    </v-btn>
                </v-system-bar>

                <div class="x-container">
                    <slot name="view"></slot>

                    <component v-if="!this.$slots.view" :is="$store.state['ui'].quickbar.view"></component>
                </div>
            </div>
        </template>
    </v-navigation-drawer>
</template>

<script>
export default {
    name: 'QuickBar',
    methods: {
        onChange(open) {
            if (!open) {
                this.$store.commit('ui/QUICKBAR_RESET');
            }
        },
    },
}
</script>

<style lang="sass" scoped>
.x-quickbar
    .x-container
        height: calc(100% - 35px)
</style>
